:root {
  --background-color: #fff;
  --main-color: #00acdc;
  --border-height: 3px;
  --font-size: 12px;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  display: block;
}

header {
  background-color: var(--background-color);
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.menu_box {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.menu_item {
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  list-style: none;
}

.menu_link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.menu_item:not(:last-child) {
  margin-right: 20px;
}

.menu_link:hover, .is-active {
  color: var(--main-color);
}

.is-active:after {
  content: " ";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.menu_link:active:after {
  content: " ";
  background-color: var(--main-color);
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
